//Mobile First 320px


.grid-container {
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: auto;
    //margin: 15px ;
    grid-template-areas:
    "header"
    "navigation"
    "dashboard"
    "main"
    "footer";
}

////////////// Header-Mobile /////////////////////


header {
    grid-area: header;
    // display: grid;
    // grid-template-columns: 2fr .5fr .2fr 2fr;

    display: flex;
    justify-content: space-between;

    background-color: $main-accent;
    color: whitesmoke;
    height: 80px;
    }


.webapp-logo {
    font-size: 24px;
    margin-top: 15px;
    padding-left: 10px;
    justify-self: flex-start;
    
  }


.vertical-line {
    height: 40px;
    //position: absolute;
    //left: 57%;
    //margin-left: -3px;
    top: 0;
    margin-top: 15px;
    border-left: 1px solid $gray;
    position: relative;

    justify-self: center;

    }

//bell-svg in images file

//Grouping of top items in header
#top-info {
    display: flex;
    justify-content: space-between;
    width: 30%;
}


//current-user and top-name
#notifications {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: 1fr;
    grid-gap: 10px;

    justify-self: center;

    //width: 70px;
   // height: 1%;
    margin-top: 15px;
    //margin-left: 5px;
    

}



.top-name {
    //margin-left: 10px;
    margin-top: 15px;
}



////////////////////Tab and dashboard
.main-header {
    grid-area: dashboard;
    //display: flex;
    height: 80px;
    //margin-top: -10px;
    display: grid;
    grid-template-columns: 1fr 2fr;
    
}

.headline {
    margin-left: 10px;
    margin-top: 20px;
}

#tab {
    background-color: rgb(226, 224, 224);
    border: 1px gray solid;
}


/////////////Navigation-Mobile/////////////////

nav {
    grid-area: navigation;
    display: flex;
    justify-content: space-between;
    background-color: rgba(80, 30, 137, 0.989);
    padding: 10px;
    }

////////////Main Section-Mobile//////////////////
main {
    grid-area: main;
    display: grid;
    grid-template-rows: auto;
    margin-left: 10px;
    }



//Long gray Dividing line
#traffic,
.daily,
.mobile,
.social-container-full,
#members,
.activity,
.message {
    border-bottom: 2px lightgrey solid;
    margin-left: -10;
    margin-right: -10;
}

//Short gray dividing line
.members-container,
.members-activity {
    border-bottom: lightgray solid 1px;
}




.social-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    border: 1px $gray solid;
    border-radius: 2%;
    background-color: $gray;
    margin: 15px
}

.social-text {
    margin-top: 10px;
}

.st-top {
    font-size: 18px;
    color: $main-accent;
    font-weight: 600;
}

.st-number {
    font-size: 40px;
}



/////Members Section-mobile

.members-container,
.members-activity {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 15px;
}

#date {
    //text-align: center;
    //align-self: center;
    margin-top: auto;
}


.members-text, 
.members-activity p,
#date {
    text-align: center;
    margin-bottom: 15px;
}

.members-text a {
    color: $main-accent;
}


