///////////Tablet Size/////////////////
@media (min-width: 768px) {
    
    .grid-container {
        grid-template-columns: 55px 1fr;
        grid-template-areas:
        "header header"
        "navigation dashboard"
        "navigation main"
        "navigation footer";
        }

        
/////////////Header-Tablet//////////////
header {
    grid-area: header;
   // grid-template-columns: 2fr .5fr .3fr 1fr;
    height: 70px;
    
}


.vertical-line {
    height: 40px;
    //margin-top: 10px;
    //margin-left: 20px;
}

.top-name {
    margin-top: 10px;
    margin-left: -4px;
    font-size: 14px;
    padding-right: 5px;
}

.bell-svg {
    margin-top: 5px;
}

#notifications {
    padding-right: 10px;
}

///////////Navigation-Tablet///////////
nav {
    grid-area: navigation;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: .03fr .03fr .03fr .03fr;

}


////////////Main Section-Tablet//////////////////////


main {
    grid-template-columns: 50%;
    //margin-left: 20px;
    margin-top: 10px;
    //padding-left: 10px;
    //margin-right: 20px;
    //padding-right: 40px;
}


//"Dashboard" on tab
.headline {
    //margin-left: 10px;
    margin-top: 10px;
    font-size: 20px;
    padding: 10px;
    //margin-left: 10px;
}

#tab {
    grid-column: 2 / span 2;
    //margin-left: 15px;
   // width: 90%;
    height: 60px;
    //margin-left: -80px;
}



.alert {
    grid-row: 1 / span 2;
    margin-top: 15px;
}

//Top Charts Daily Traffic and Mobile Users
.daily,
.mobile {
    border: 1px lightgray solid;
}


#traffic {
    padding-left: 15px;
}

.traffic-nav {
    display: flex;
    justify-content: space-around;
}

///////////SOCIAL SECTION/////////////////

//All 3 social media boxes, no header
.social-container-full {
    display: flex;
    grid-column: 1 / span 2;
    //align-items: center;
    //justify-content: space-between;
    width: 90vw;
}

//each individual FB/INS/TW Box
.social-container {
   width: 60vw;
   border: 3px $light-gray solid;
   margin-left: 2px;
}

//each individual social svg
.social-image {
    width: 50px;
    margin-right: 10px;
    margin-left: 10px;
    align-self: center;
    //background-color: $main-accent;
    //fill: $gray;
   // border-radius: 50%;
}

.social-text {
    padding: 10px;
}

